<template>
    <div id="main">
        <div id="banner">
            <div id="intro">
                <h1>{{ greeting }}</h1>
                <h1>{{ welcome }}</h1>
            </div>            
        </div>

        <div id="content">
            <p>Hello, my name is Walu. <br/>
            I am an IT Graduate and I aspire to be a Data Scientist. <br/>
            I am currently pursuing a Bachelor's degree in Data Science and Applications from IIT, Madras. <br/>
            </p>

            <h2>Languages</h2>
            <p>Java, Kotlin, Python, HTML, CSS</p>

            <h2>Frameworks</h2>
            <p>Flask, Vue</p>

            <h2>Cloud and Databases</h2>
            <p>SQL, Firebase</p>

            <h2>Tools</h2>
            <p>Numpy, Pandas, Matplotlib, Git, Canva</p>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'home-page',
        data() {
            return {
                greeting: "",
                welcome: ""
            }
        },

        methods: {
            typeText1() {
                let i = 0; //index of character
                let msg1 = "Hi, I'm Walu Aier."
                let timeInterval = 60

                const msg1Interval = setInterval(() => {
                    if (i < msg1.length) {
                        this.greeting += msg1.charAt(i);
                        i++;
                    } else {
                        clearInterval(msg1Interval);

                        setTimeout(() => {
                            this.typeText2()
                        }, 500)
                    }

                }, timeInterval); // Adjust the interval to control typing speed
            },

            typeText2() {
                this.greeting = ""
                let i = 0; //index of character
                let timeInterval = 60
                let msg2 = "Welcome to my page!" 
                const msg2Interval = setInterval(() => {
                    if (i < msg2.length) {
                        this.greeting += msg2.charAt(i);
                        i++;
                    } else {
                        clearInterval(msg2Interval);
                    }

                }, timeInterval); // Adjust the interval to control typing speed
            }
        },

        created() {
            this.typeText1();
        },
    }
</script>

<style scoped>
    h1 {
        font-size: 70px;
    }

    #banner {
        background: white;
        padding: 10px;
        height: 140px;
        padding: 10px;
    }
  
    #intro {
        text-align: left;
        /* background: pink; */
        width: 65%;
    }

    #content {
        background: rgb(240, 239, 239);
        padding: 10px;
    }

    p {
        font-size: 18px;
    }
</style>

  